body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  background-color: transparent;
  font-size: inherit;
  font-family: inherit;
  box-sizing: border-box;
  text-align: left; }

button {
  cursor: pointer; }

a {
  color: inherit;
  text-decoration: none; }

h1, h2, h3, h4, h5 {
  font-family: inherit;
  text-align: left; }

li {
  list-style: none; }

body {
  font-family: 'Inter';
  font-size: 14px;
  color: #000000;
  background-color: #E7F6FF;
  padding: 20px 0; }

.wrapper {
  max-width: 1080px;
  margin: 0 auto;
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px; }

.container {
  max-width: 980px;
  padding: 0 10px;
  margin: 0 auto; }

.btn-block {
  display: flex;
  height: 55px;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
  background-color: #9DD458;
  border-radius: 18px;
  padding: 0 30px;
  cursor: pointer;
  transition: all 0.2s; }
  .btn-block span {
    display: inline-block;
    padding-right: 50px;
    background-image: url(/shop-sn/static/media/ar-r.3ec39de3.svg);
    background-repeat: no-repeat;
    background-position: right; }
  .btn-block:hover {
    -webkit-transform: translate(0, -1px);
            transform: translate(0, -1px); }

.header {
  border-bottom: 1px solid #EAEAEA;
  margin: 0 0 45px 0; }
  .header__body {
    min-height: 130px;
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header__right {
    display: flex;
    align-items: center; }
    .header__right button, .header__right a {
      margin-right: 30px; }
      .header__right button:last-child, .header__right a:last-child {
        margin-right: 0; }
  .header__cart {
    display: flex;
    align-items: center; }
    .header__cart img {
      margin-right: 10px; }
  .header__icon {
    width: 20px;
    height: 20px;
    object-fit: contain;
    cursor: pointer; }

.cart {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.52); }
  .cart__body {
    max-width: 385px;
    height: 100%;
    margin-left: auto;
    background-color: #fff;
    padding: 30px; }
  .cart__wrapper {
    height: 100%;
    display: flex;
    flex-direction: column; }
  .cart__list {
    flex: 1 1 auto;
    overflow-y: auto; }
  .cart__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #F3F3F3;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 30px 20px;
    margin: 0 0 20px 0; }
  .cart__content {
    margin: 0 15px;
    flex: 1 1 auto; }
  .cart__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    text-align: left;
    margin: 0 0 30px 0; }
  .cart__img {
    width: 70px;
    object-fit: contain;
    flex: 0 0 70px; }
  .cart__name {
    font-weight: 400;
    margin: 0 0 8px 0; }
  .cart__price {
    display: block;
    font-weight: 700; }
  .cart__close {
    width: 32px;
    flex: 0 0 32px;
    height: 32px;
    border: 1px solid #DBDBDB;
    border-radius: 8px;
    background-image: url(/shop-sn/static/media/close.48c816d6.svg);
    background-repeat: no-repeat;
    background-position: center;
    transition: all 0.2s;
    cursor: pointer; }
    .cart__close:hover {
      background-color: #9DD458; }
    .cart__close_m {
      margin-left: auto; }

.total-cart__line {
  display: flex;
  align-items: flex-end;
  margin: 0 0 20px 0; }

.total-cart__name {
  position: relative; }

.total-cart__dashed {
  flex: 1 1 auto;
  height: 1px;
  margin: 0 5px;
  border: 1px dashed #DFDFDF;
  display: block; }

.total-cart__btn {
  width: 100%; }

.nathing {
  text-align: center;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
  .nathing__img {
    width: 120px;
    height: 120px;
    object-fit: cover;
    margin: 0 0 20px 0; }
  .nathing__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    margin: 0 0 10px 0; }
  .nathing__text {
    font-size: 16px;
    line-height: 24px;
    opacity: 0.4;
    margin: 0 0 30px 0; }
  .nathing__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: #9DD458;
    border-radius: 18px;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: #fff;
    height: 55px;
    padding: 0 30px; }
    .nathing__btn span {
      padding-left: 35px;
      background-image: url(/shop-sn/static/media/l.87cacf00.svg);
      background-position: left;
      background-repeat: no-repeat; }

.top-block__row {
  display: flex;
  justify-content: space-between;
  margin: 0 0 45px 0; }

.top-block__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #000000; }

.top-block__search {
  border: 1px solid #F3F3F3;
  border-radius: 10px;
  width: 250px;
  height: 45px;
  display: flex; }

.top-block__icon {
  width: 45px;
  height: 100%;
  background-image: url(/shop-sn/static/media/search.be41b755.svg);
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer; }

.top-block__inp {
  width: 100%;
  height: 100%;
  color: #C4C4C4; }

.products__row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px; }

.products__column {
  flex: 0 1 25%;
  padding: 0 20px;
  margin: 0 0 40px 0; }

.products__items {
  border: 1px solid #F3F3F3;
  box-sizing: border-box;
  border-radius: 40px;
  padding: 20px; }

.products__title {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  margin: 0 0 10px 0; }

.products__img {
  width: 100%;
  height: 112px;
  object-fit: contain; }

.products__footer {
  display: flex;
  justify-content: space-between; }

.products__add {
  cursor: pointer; }

.products__number {
  font-weight: 700; }

.slider {
  background: #F4EFE9;
  border-radius: 20px;
  padding: 20px;
  margin: 0 0 60px 0; }
  .slider__row {
    display: flex; }
  .slider__column:first-child {
    flex: 0 1 40%; }
  .slider__column:last-child {
    flex: 0 1 60%; }
  .slider__title {
    font-style: normal;
    font-weight: 700;
    font-size: 38px;
    line-height: 46px;
    color: #8BB43C;
    margin: 0 0 15px 0; }
  .slider__content {
    padding-top: 20px;
    padding-left: 20px; }
  .slider__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    background: #A5D364;
    border-radius: 110px;
    height: 45px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 30px; }

.fav__title {
  margin: 0 0 40px 0; }

.fav__body {
  margin: 0 auto;
  padding-bottom: 40px; }

